"use client";

import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
  CartesianGrid,
} from "recharts";
import { TrendingUp } from "lucide-react";
import { cn } from "@/lib/utils";
import { TrendDataPoint } from "@/types";

interface SentimentAreaChartProps {
  data: TrendDataPoint[];
  currentScore: number;
  change24h: number;
}

export function SentimentAreaChart({
  data,
  currentScore,
  change24h,
}: SentimentAreaChartProps) {
  const isPositive = change24h >= 0;

  return (
    <Card className="card-crypto border-0">
      <CardHeader className="flex flex-row items-center justify-between pb-2">
        <div>
          <CardTitle className="text-lg text-muted-foreground font-medium mb-2">
            Market Sentiment Index
          </CardTitle>
          <div className="flex items-center gap-3">
            <span className="data-value">{currentScore.toFixed(2)}</span>
            <Badge
              variant={isPositive ? "success" : "destructive"}
              className="flex items-center gap-1"
            >
              <TrendingUp
                className={cn("w-3 h-3", !isPositive && "rotate-180")}
              />
              {isPositive ? "+" : ""}
              {change24h.toFixed(2)}%
            </Badge>
          </div>
        </div>

        {/* Time Range Selector */}
        <div className="flex gap-2">
          {["Day", "Week", "Month", "Year"].map((period, idx) => (
            <button
              key={period}
              className={cn(
                "px-4 py-2 rounded-button text-sm font-medium transition-all",
                idx === 0
                  ? "bg-primary text-white"
                  : "bg-background-tertiary text-muted-foreground hover:text-foreground"
              )}
            >
              {period}
            </button>
          ))}
        </div>
      </CardHeader>

      <CardContent className="pt-6">
        <ResponsiveContainer width="100%" height={300}>
          <AreaChart data={data}>
            <defs>
              <linearGradient id="colorScore" x1="0" y1="0" x2="0" y2="1">
                <stop offset="5%" stopColor="#FF6B4A" stopOpacity={0.8} />
                <stop offset="95%" stopColor="#8B4513" stopOpacity={0.1} />
              </linearGradient>
            </defs>
            <CartesianGrid strokeDasharray="3 3" stroke="#333" />
            <XAxis dataKey="time" stroke="#666" style={{ fontSize: "12px" }} />
            <YAxis stroke="#666" style={{ fontSize: "12px" }} />
            <Tooltip
              contentStyle={{
                backgroundColor: "#242424",
                border: "1px solid #333",
                borderRadius: "8px",
                color: "#fff",
              }}
            />
            <Area
              type="monotone"
              dataKey="score"
              stroke="#FF6B4A"
              strokeWidth={2}
              fillOpacity={1}
              fill="url(#colorScore)"
            />
          </AreaChart>
        </ResponsiveContainer>
      </CardContent>
    </Card>
  );
}
